body {
  input {
  width: 50vw;
  padding: 2px;
  box-sizing: border-box;
  }
}


#center {
  background: rgb(0, 0, 0);
  border-radius: 25px;
  width: 45vw;
  height: 85vh;
  margin: 0 auto;
  h1 {
    color: white;
  }
}

#Image {
  width: 50%;
  height: 50%;
  border-radius: 20px;
}

#leftHalf {
  background-image: linear-gradient(to left, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0)), url("../elements/MatchImage.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  width: 50vw;
  height: 100vh;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: -1;

}

#rightHalf {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0)), url("../elements/PitImage.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  width: 50vw;
  height: 100vh;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: -1;
}

#Scouting1 {
  margin-top: 5%;
  font-size: 400%;
  color: black;
}

#a4079-1 {
  width: 25%;
  border-radius: 10%;
}

#Scouting,
#Survey {
  color: white;
  height: 100px;
  border-radius: 20px;
  background-color: black;
  margin-left: 25px;
  border: 2px solid rgb(0, 0, 0);
}

#Scouting h1 {
  font-size: 200%;
}

#Survey h1 {
  font-size: 200%;
}

#buttons {
  padding: 20px;
}

.submitbox {
  text-align: center;
  width: 50%;
  height: 50%;
  background-color: #f1f1f1;
  border: 2px solid #ccc;
  border-radius: 5px;
  padding: 16px;
  margin: 0 auto;
  display: none;

}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  z-index: 9999;
  animation: fadeOut 1s linear forwards;
}

@keyframes fadeOut {
  from {
      opacity: 1;
  }

  to {
      opacity: 0;
      visibility: hidden;
  }
}

.fade-in {
  animation: fadeIn 0.5s linear forwards;
}

@keyframes fadeIn {
  from {
      opacity: 0;
  }

  to {
      opacity: 1;
  }
}

[data-animate] {
  animation-name: slide-up;
  animation-duration: 0.5s;
  animation-fill-mode: both;
}

@keyframes slide-up {
  from {
      transform: translateY(50px);
      opacity: 0;
  }
  to {
      transform: translateY(0);
      opacity: 1;
  }
}

@media screen and (max-width: 1400px) {

  body {
      position: center;
  }

  #center {
      background: rgba(0, 0, 0, 0.75);
      align-items: center;
      border-radius: 10px;
      width: 90%;
      background-color: black;
      box-sizing: border-box;
  }

  #Image {
      width: 200px;
      height: 200px;
      border-radius: 20px;
  }

  #buttons {
      padding-top: 5%;
      padding-bottom: 5%;
  }

  #leftHalf {
      background-image: linear-gradient(to top, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0)), url("../elements/MatchImage.jpg");
      background-repeat: no-repeat;
      background-size: cover;
      width: 100vw;
      height: 50vh;
      position: absolute;
      left: 0;
      top: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      z-index: -1;
  }

  #rightHalf {
      background-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(255, 255, 255)), url("../elements/PitImage.jpg");
      background-repeat: no-repeat;
      margin: 0;
      background-size: cover;
      width: 50vw;
      height: 100vh;
      position: absolute;
      bottom: 0;
      right: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      z-index: -1;
  }

  #Scouting,
  #Survey {
      color: white;
      height: 100px;
      width: 50%;
      border-radius: 20px;
      background-color: black;
      margin: auto;
  }

  #Scouting h1 {
      font-size: 200%;
  }

  #Survey h1 {
      font-size: 150%;
  }
}